.projects {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.projects .headline{
  color: #4b9cd3;
}
.projects .projectTitle {
  text-align: center;
  color: #3e497a;
  font-size: 60px;
  font-weight: bolder;
}

.projectList {
  width: auto;
  height: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  place-items: center;
}

/* MENU ITEM STYLING */

.projectItem {
  border-radius: 15px;
  width: 300px;
  height: 350px;
  margin: 40px;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  text-align: center;
  position: relative;
  font-weight: normal !important;
}
.projectItem:hover {
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.5);
  transition: 0.3s ease-in;
  cursor: pointer;
}

.projectItem .bgImage {
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  width: 100%;
  height: 200px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.projectItem .project-header-container{
  height: 15%;
}
.projectItem h1 {
  font-size: 20px;
  align-items: center;
  line-height:120%;
}

.projectItem p {
  font-size: 15px;
  line-height: 100%;
  margin-top: 0%;

}

.projectItem p.date{
  position: absolute;
  font-size: 15px;
  text-align: left;
  bottom: 1%;
  left: 2%;
  margin-left: 30px;
  /* display: inline-block; */
  width:100px;
}

.projectItem p.skill{
  position: absolute;
  font-size: 15px;
  bottom: 1%;
  right: 40px;
  /* left: 50%; */
  display: inline-block;
  text-decoration: underline;
  /* text-decoration: green wavy underline; */
  font-style: italic;
}

@media only screen and (max-width: 1300px) {
  .projectList {
    grid-template-columns: 1fr 1fr;
  }
}

@media only screen and (max-width: 800px) {
  .projectList {
    grid-template-columns: 1fr;
  }
}

@media only screen and (max-width: 800px) {
  .projectList {
    width: 100%;
  }
  .projectItem {
    width: 300px;
    height: 300px;
  }
}

